





























/* this will be used as global.css */
body {
  background-color: #333;
  color: #fff;
  font-family: Arial, Helvetica, sans-serif;
}
header {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
header > div >img { 
  width: 150px;
  height: auto;
}
header > h1 {
  width: 15vw;
  margin: 10vh 3vw 0 5vw;
  text-align: center;
}
h2 {
  font-weight: normal;
  text-align: center;
}
a {
  text-decoration: none;
  color: inherit;
}
